import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {GuiManagerProvider} from "../../providers/gui-manager/gui-manager";
import {AuthManagerProvider} from "../../providers/auth-manager/auth-manager";
import {ApiProvider} from "../../providers/api/api";

/**
 * Generated class for the UserSettingsChangePasswordPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-user-settings-change-password',
  templateUrl: 'user-settings-change-password.html',
})
export class UserSettingsChangePasswordPage {

  private currentPass = '';
  private newPass1 = '';
  private newPass2 = '';

  constructor(public navCtrl: NavController, public navParams: NavParams, private guiManager: GuiManagerProvider, private authManager: AuthManagerProvider, private http: ApiProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad UserSettingsChangePasswordPage');
  }

  resetPass() {

    this.http.post("/reset/direct", {
      current: this.currentPass,
      pass: this.newPass1,
      pass2: this.newPass2
    }, [], (success, data) => {

      if (data.success) {
        if (data.data == "updated") {
          this.guiManager.showToast("Passwort aktualisiert", 5000);
        }
      } else {
        if (data.data == "pass_not_same") {
          this.guiManager.showToast("Passwörter stimmen nicht überrein!", 10000);

        }
        if (data.data == "pass_short") {
          this.guiManager.showToast("Neues Passwort muss mindestens 5 zeichen lang sein!!", 10000);

        }
        if (data.data == "current_false") {
          this.guiManager.showToast("Aktuelles Passwort falsch!", 10000);

        }
      }

    }, this.authManager.key, true)

  }

}



